<template>
  <div class="movie-item mb-3">
    <div class="movie-item-poster" :style="posterBG"></div>
    <div class="movie-info-wrap">
      <div class="movie-item-info">
        <h3 class="movie-title">{{ movie.Title }}</h3>
        <span class="movie-year">{{ movie.Year }}</span>
      </div>
      <div class="movie-item-controls row" v-show="!isSearch">
        <!-- <div class="col">
          <BButton size="md" block variant="outline-light">Edit</BButton>
        </div> -->
        <div class="col">
          <BButton size="md" block variant="outline-light" @click="emitRemoveItem">Remove</BButton>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'MovieItem',
  props: {
    movie: {
      type: Object,
      required: true,
    },
  },
  computed: {
    ...mapGetters('movies', ['isSearch']),
    posterBG() {
      return {
        // eslint-disable-next-line quotes
        'background-image': `url(${this.movie.Poster})`,
      };
    },
  },
  methods: {
    emitRemoveItem() {
      this.$emit('removeMovie', { id: this.movie.imdbID, title: this.movie.Title });
    },
  },
};
</script>

<style scoped>
.movie-item {
  position: relative;
  cursor: pointer;
  border-radius: 5px;
  overflow: hidden;
  transition: all 0.2s ease;
  height: 400px;
}

.movie-item:hover {
  box-shadow: 0px 5px 30px rgba(0, 0, 0, 0.7);
  transform: scale(1.02);
}

.movie-item-poster {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  z-index: -1;
}

.movie-info-wrap {
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  opacity: 0;
  transition: all 0.2s ease;
}

.movie-item:hover .movie-info-wrap {
  opacity: 1;
  background-color: rgba(0, 0, 0, 0.7);
}

.movie-title {
  font-size: 18px;
  color: #fff;
}

.movie-year {
  font-size: 14px;
  color: #fff;
}
</style>
